// imports
import axios from "axios";
import { AuthenticatedHeaders } from "utils/ApiHeaders";

import { API, API_V2 } from "../../Backend";

// const NODE_ENV = "dev";
let BE_URL = API;

//${API_V2}/fb/pages/${business_id}
// requests defs
const saveFBUserData = async (fbData) => {
  try {
    const business_id = localStorage.getItem("businessId");
    const res = await fetch(`${API_V2}/fb/pages/${business_id}`, {
      method: "POST",
      headers: AuthenticatedHeaders(),
      body: JSON.stringify(fbData),
    });
    return await res.json();
  } catch (error) {
    return { message: error.message };
  }
};

const fetchFBUserPages = (fbUserID, access_token) =>
  new Promise(async (resolve, reject) => {
    try {
      // FB request URL
      const pageURL = `https://graph.facebook.com/${fbUserID}/accounts?access_token=${access_token}`;

      const FetchPagesListRes = await axios({ url: pageURL });

      const { data: pageData } = FetchPagesListRes;

      if (pageData.error) {
        return reject({ error: pageData.error });
      }

      return resolve({ pageData });
    } catch (error) {
      return reject({ message: error.message });
    }
  });

const fetchBusinessFBPages = async (fbUserId) => {
  try {
    const res = await fetch(`${API_V2}/fb/facebook-pages/${fbUserId}`, {
      method: "GET",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
        Authorization: `bearer ${localStorage.getItem("token")}`,
        "X-BusinessId": localStorage.getItem("businessId"),
      },
    });
    return await res.json();
  } catch (err) {
    return { message: err.message };
  }
};

