const saveFBPageData = (pageInfo) =>
  new Promise(async (resolve, reject) => {
    try {
      const saveFBPageRes = await axios({
        url: `${API_V2}/fb/account`,
        method: "POST",
        data: pageInfo,
        headers: {
          "Content-Type": "application/json",
          "X-BusinessId": localStorage.getItem("businessId"),
          Authorization: `Bearer ${localStorage.getItem("token")}`,
        },
      });

      const { data } = saveFBPageRes;

      if (data.error) {
        return reject({ error: data.error });
      }

      const { success } = data;

      return resolve({ success });
    } catch (error) {
      return reject({ message: error.message });
    }
  });

const fetchProfileImg = (pageID, accessToken) =>
  new Promise(async (resolve, reject) => {
    const fbURL = `https://graph.facebook.com/v11.0/${pageID}/picture`;

    try {
      const FetchImgRes = await axios({ url: fbURL });

      const { data: imgData } = FetchImgRes;

      if (imgData.error) {
        return reject({ error: imgData.error });
      }

      const { url } = imgData;

      return resolve({ url });
    } catch (error) {
      return reject({ message: error.message });
    }
  });

