const fetchPlatformPermissions = (platform) =>
  new Promise(async (resolve, reject) => {
    try {
      const permissionRes = await axios({
        url: `${BE_URL}/smSetup/permissions`,
        method: "POST",
        data: {
          platform,
        },
        headers: {
          "Content-Type": "application/json",
          "X-BusinessId": localStorage.getItem("businessId"),
          "X-Auth-Token": localStorage.getItem("token"),
        },
      });

      const { data: permissionData } = permissionRes;

      const { error, permissions } = permissionData;

      if (error) {
        return reject({ error });
      }

      return resolve({ permissions });
    } catch (error) {
      return reject({ message: error.message });
    }
  });

